.app-header {
  align-items: flex-end;
}

.app-header h1 {
  flex-grow: 1;
}

.app-header h2 {
  font-size: 1.2rem;
  color: grey;
}
.todo-list-item{
  margin-bottom: 0;
}
.todo-list-item-label{
  vertical-align: middle;
  padding-top: 10px ;
  text-align: left;
}
.done .todo-list-item-label{
  text-decoration: line-through;
}

.important .todo-list-item-label{
  color: tomato;
  font-weight: 700;
}
.todo-list-item-btn{
  text-align: right;
}
.button-group{
  margin: 5px 0 0 ;
}
.button-group button{
  margin: 0 5px;
}

.app{
  width: 100%;
  max-width: 600px;
  height: auto;
  display: block;
  margin: 0 auto;
  text-align: center;

}
.m5 {
margin:0 5px;
}
